/* обнулим ширину */
.is-layout-constrained .wp-block-blocks-gamestore-recent-news {
	max-width: calc(100% + (var(--wp--style--root--padding-right) + var(--wp--style--root--padding-left)))!important;
	margin-right: calc(var(--wp--style--root--padding-right) * -1) !important;
	margin-left: calc(var(--wp--style--root--padding-left) * -1) !important;
	margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
	margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
}

.wp-block-blocks-gamestore-recent-news{
	text-align: center;
	position: relative;
	background-size: cover;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(180deg, var(--background-default) 0%, var(--background-80) 50.06%, var(--background-default) 100%);
		z-index: 0;
	}

	h2 {
		line-height: 1.4;
		background: var(--accent-gradient);
		background-clip: text;
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		margin-top: 0;
		margin-bottom: 24px;
		text-align: center;
		width: fit-content; // чтобы ширина была не 100%, а по размеру контента (это нужно для градиента, иначе будет простой цвет)
		position: relative;
		z-index: 1;
		display: inline-block;
	}

	p {
		max-width: 980px;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 48px;
		position: relative;
		z-index: 1;
		color: var(--text-secondary, rgba(14, 13, 15, 0.70));
	}

	.recent-news {
		position: relative;
		z-index: 1;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		gap: 48px;

		.news-item {
			width: calc(33.3333% - 32px);
			overflow: hidden;
			border-radius: 8px;
			position: relative;
			background: var(--background-default, #FAFAFA);
			box-shadow: 0 24px 32px 0 var(--shadow-24, rgba(14, 13, 15, 0.08));
			padding-top: 48px;
			padding-bottom: 48px;
			display: flex;
			flex-direction: column;
			gap: 24px;
			align-items: center;

			&::before {
				content: "";
				width: 100%;
				position: absolute;
				top:0;
				left:0;
				height: 10px;
				background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
			}

			h3 {
				font-size: 36px;
				font-style: normal;
				background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
				background-clip: text;
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				text-align: center;
				padding-left: 48px;
				padding-right: 48px;
				overflow: hidden;
				display: block;
				max-height: 6.25rem;
				line-height: 3.12rem;
				text-overflow: ellipsis;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}

			.news-thumbnail {
				position: relative;
				aspect-ratio: 16/9;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

				.blur-image {
					position: absolute;
					top:0;
					left:0;
					width: 100%;
					height: 100%;
					filter: blur(24px);
				}
				.original-image {
					position: relative;
					z-index: 1;
				}
			}

			.news-excerpt {
				padding-left: 48px;
				padding-right: 48px;
				color: var(--text-secondary, rgba(14, 13, 15, 0.70));
				line-height: 1.7;
				overflow: hidden;
				display: block;
				max-height: 8.5rem;
				text-overflow: ellipsis;
				-webkit-line-clamp: 4;
				-webkit-box-orient: vertical;
			}

			.read-more {
				display: inline-flex;
				border-radius: 5px;
				background: var(--accent-gradient, linear-gradient(90deg, #3DAFE2 0%, #6F76C0 100%));
				box-shadow: 0px 16px 24px 0px rgba(61, 175, 226, 0.48);
				padding: 20px 32px;
				justify-content: center;
				align-items: center;
				gap: 10px;
				font-size: 14px;
				font-style: normal;
				font-weight: 700;
				line-height: 1;
				letter-spacing: 2.1px;
				text-transform: uppercase;
				color: #FFF;
				text-decoration: none;
				transition: all 300ms ease-in-out;

				&:hover {
					background: linear-gradient(90deg, #6F76C0 0%, #575ead 100%);
				}

				&::before {
					content:"";
					width: 24px;
					height: 24px;
					display:block;
					background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"><path d="M16 2H8C4.5 2 3 4 3 7V17C3 20 4.5 22 8 22H16C19.5 22 21 20 21 17V7C21 4 19.5 2 16 2ZM8 12.25H12C12.41 12.25 12.75 12.59 12.75 13C12.75 13.41 12.41 13.75 12 13.75H8C7.59 13.75 7.25 13.41 7.25 13C7.25 12.59 7.59 12.25 8 12.25ZM16 17.75H8C7.59 17.75 7.25 17.41 7.25 17C7.25 16.59 7.59 16.25 8 16.25H16C16.41 16.25 16.75 16.59 16.75 17C16.75 17.41 16.41 17.75 16 17.75ZM18.5 9.25H16.5C14.98 9.25 13.75 8.02 13.75 6.5V4.5C13.75 4.09 14.09 3.75 14.5 3.75C14.91 3.75 15.25 4.09 15.25 4.5V6.5C15.25 7.19 15.81 7.75 16.5 7.75H18.5C18.91 7.75 19.25 8.09 19.25 8.5C19.25 8.91 18.91 9.25 18.5 9.25Z" fill="white"/></svg>');
				}
			}
		}
		img {
			max-width: 100%;
		}
	}
}


/*
	Градиент задаётся как фон заголовка.
	background-clip: text заставляет фон показываться только в области букв.
	-webkit-text-fill-color: transparent делает сами буквы прозрачными, чтобы виден был только градиентный фон внутри их формы.
*/
